"use client"

import React, { useState } from "react"
import { calculateFinalPrice } from "@/lib/utils"

interface Product {
  id: string
  code: string
  name: string
  price?: number
  type: "minorista" | "mayorista"
  calculationParams?: {
    costoInicial: number
    gastos: number
    ganancia: number
    publicidad: number
    ingresosBrutos: number
    costoFijoML: number
    mercadolibre: number
    costoFinanciero: number
    pagoNube: number
    iva: number
    comisionTiendaNube: number
  }
}

interface MassParameterEditorProps {
  products: Product[]
  onUpdateProducts: (updatedProducts: Product[]) => void
}

export default function MassParameterEditor({
  products,
  onUpdateProducts,
}: MassParameterEditorProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [newParams, setNewParams] = useState({
    costoInicial: 4800,
    gastos: 10,
    ganancia: 3500,
    publicidad: 10,
    ingresosBrutos: 5,
    costoFijoML: 1800,
    mercadolibre: 16,
    costoFinanciero: 5,
    pagoNube: 5,
    iva: 21,
    comisionTiendaNube: 5,
  })
  const [selectedType, setSelectedType] = useState<"all" | "minorista" | "mayorista">("all")
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)

  function handleMassUpdate() {
    setError(null)
    setSuccess(null)

    try {
      const updatedProducts = products.map((product) => {
        // Skip products that don't match the selected type filter
        if (selectedType !== "all" && product.type !== selectedType) {
          return product
        }

        // Skip products without calculation parameters
        if (!product.calculationParams) {
          return product
        }

        // Use the product's original costo inicial, but update other parameters
        const updatedParams = {
          ...newParams,
          costoInicial: product.calculationParams.costoInicial, // Preserve original costo inicial
        }

        // Calculate new price with updated parameters (keeping original costo inicial)
        const newPrice = calculateFinalPrice(updatedParams)

        return {
          ...product,
          price: newPrice,
          calculationParams: updatedParams,
        }
      })

      onUpdateProducts(updatedProducts)
      
      const affectedCount = products.filter(p => 
        (selectedType === "all" || p.type === selectedType) && p.calculationParams
      ).length

      setSuccess(`Se actualizaron ${affectedCount} productos correctamente.`)
    } catch (e) {
      if (e instanceof Error) {
        setError(e.message)
      }
    }
  }

  if (!isOpen) {
    return (
      <div className="max-w-6xl mx-auto p-6 bg-white rounded-md shadow-md mt-8">
        <div className="flex justify-between items-center">
          <h2 className="text-2xl font-semibold">Edición Masiva de Parámetros</h2>
          <button
            onClick={() => setIsOpen(true)}
            className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
          >
            Abrir Editor Masivo
          </button>
        </div>
        <p className="text-gray-600 mt-2">
          Modifica parámetros de cálculo y actualiza precios de múltiples productos a la vez.
        </p>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto p-6 bg-white rounded-md shadow-md mt-8">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-2xl font-semibold">Edición Masiva de Parámetros</h2>
        <button
          onClick={() => setIsOpen(false)}
          className="px-4 py-2 bg-gray-600 text-white rounded hover:bg-gray-700 transition"
        >
          Cerrar
        </button>
      </div>

      {/* Filter Selection */}
      <div className="mb-6">
        <label className="block text-sm font-medium mb-2">Aplicar cambios a:</label>
        <select
          value={selectedType}
          onChange={(e) => setSelectedType(e.target.value as "all" | "minorista" | "mayorista")}
          className="p-2 border rounded"
        >
          <option value="all">Todos los productos</option>
          <option value="minorista">Solo productos minoristas</option>
          <option value="mayorista">Solo productos mayoristas</option>
        </select>
      </div>

      {/* Parameter Inputs */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
        <label className="flex flex-col">
          Costo Inicial (solo para referencia)
          <input
            type="number"
            value={newParams.costoInicial}
            onChange={(e) => setNewParams({ ...newParams, costoInicial: Number(e.target.value) })}
            className="mt-1 p-2 border rounded bg-gray-100"
            disabled
          />
          <span className="text-xs text-gray-500 mt-1">
            Cada producto mantendrá su costo inicial original
          </span>
        </label>
        <label className="flex flex-col">
          Gastos (%)
          <input
            type="number"
            value={newParams.gastos}
            onChange={(e) => setNewParams({ ...newParams, gastos: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Ganancia ($)
          <input
            type="number"
            value={newParams.ganancia}
            onChange={(e) => setNewParams({ ...newParams, ganancia: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Publicidad (%)
          <input
            type="number"
            value={newParams.publicidad}
            onChange={(e) => setNewParams({ ...newParams, publicidad: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Ingresos Brutos (%)
          <input
            type="number"
            value={newParams.ingresosBrutos}
            onChange={(e) => setNewParams({ ...newParams, ingresosBrutos: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Costo Fijo ML ($)
          <input
            type="number"
            value={newParams.costoFijoML}
            onChange={(e) => setNewParams({ ...newParams, costoFijoML: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          MercadoLibre (%)
          <input
            type="number"
            value={newParams.mercadolibre}
            onChange={(e) => setNewParams({ ...newParams, mercadolibre: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Costo Financiero (%)
          <input
            type="number"
            value={newParams.costoFinanciero}
            onChange={(e) => setNewParams({ ...newParams, costoFinanciero: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Pago Nube (%)
          <input
            type="number"
            value={newParams.pagoNube}
            onChange={(e) => setNewParams({ ...newParams, pagoNube: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          IVA (%)
          <input
            type="number"
            value={newParams.iva}
            onChange={(e) => setNewParams({ ...newParams, iva: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Comisión Tienda Nube (%)
          <input
            type="number"
            value={newParams.comisionTiendaNube}
            onChange={(e) => setNewParams({ ...newParams, comisionTiendaNube: Number(e.target.value) })}
            className="mt-1 p-2 border rounded"
          />
        </label>
      </div>

      {/* Action Buttons */}
      <div className="flex gap-4 mb-4">
        <button
          onClick={handleMassUpdate}
          className="px-6 py-2 bg-green-600 text-white rounded hover:bg-green-700 transition"
        >
          Aplicar Cambios Masivos
        </button>
      </div>

      {/* Messages */}
      {error && <p className="text-red-600 mb-4">{error}</p>}
      {success && <p className="text-green-600 mb-4">{success}</p>}

      {/* Preview */}
      <div className="bg-gray-50 p-4 rounded-md">
        <h3 className="font-semibold mb-2">Vista Previa:</h3>
        <p className="text-sm text-gray-600">
          Productos que serán afectados: {
            products.filter(p => 
              (selectedType === "all" || p.type === selectedType) && p.calculationParams
            ).length
          }
        </p>
        <p className="text-sm text-gray-600">
          <strong>Nota:</strong> El costo inicial de cada producto se mantendrá, solo se actualizarán los demás parámetros.
        </p>
        <p className="text-sm text-gray-600">
          Ejemplo de precio con parámetros nuevos: ${(() => {
            try {
              return calculateFinalPrice(newParams).toFixed(2)
            } catch {
              return "Error en cálculo"
            }
          })()} (usando costo inicial de ejemplo: ${newParams.costoInicial})
        </p>
      </div>
    </div>
  )
}
