export function calculateFinalPrice({
  costoInicial,
  ganancia,
  gastos,
  publicidad,
  ingresosBrutos,
  costoFijoML,
  mercadolibre,
  costoFinanciero,
  pagoNube,
  iva,
  comisionTiendaNube,
}: {
  costoInicial: number
  ganancia: number
  gastos: number
  publicidad: number
  ingresosBrutos: number
  costoFijoML: number
  mercadolibre: number
  costoFinanciero: number
  pagoNube: number
  iva: number
  comisionTiendaNube: number
}): number {
  // Convert percentages to decimals
  const pagoNubeConIva = pagoNube * (1 + iva / 100)
  const sumaPorcentajes =
    (gastos +
      publicidad +
      ingresosBrutos +
      mercadolibre +
      costoFinanciero +
      pagoNubeConIva +
      comisionTiendaNube) /
    100

  const denominador = 1 - sumaPorcentajes

  if (denominador <= 0) {
    throw new Error(
      'La suma de los porcentajes es demasiado alta, no se puede calcular el precio final.'
    )
  }

  const precioFinal =
    (costoInicial + ganancia + costoFijoML) / denominador

  return precioFinal
}
