"use client"

import React, { useState, useEffect } from "react"
import CalculatorForm from "@/components/CalculatorForm"
import ProductForm from "@/components/ProductForm"
import ProductList from "@/components/ProductList"
import MassParameterEditor from "@/components/MassParameterEditor"
import { v4 as uuidv4 } from "uuid"

interface Product {
  id: string
  code: string
  name: string
  price?: number
  type: "minorista" | "mayorista"
  calculationParams?: {
    costoInicial: number
    gastos: number
    ganancia: number
    publicidad: number
    ingresosBrutos: number
    costoFijoML: number
    mercadolibre: number
    costoFinanciero: number
    pagoNube: number
    iva: number
    comisionTiendaNube: number
  }
}

export default function HomePage() {
  const [products, setProducts] = useState<Product[]>([])
  const [calculationResult, setCalculationResult] = useState<{
    price: number | null
    params: Product["calculationParams"] | null
  }>({ price: null, params: null })

  // Load products from localStorage on mount
  useEffect(() => {
    const stored = localStorage.getItem("products")
    if (stored) {
      setProducts(JSON.parse(stored))
    }
  }, [])

  // Save products to localStorage on change
  useEffect(() => {
    localStorage.setItem("products", JSON.stringify(products))
  }, [products])

  function addProduct(product: {
    code: string
    name: string
    type: "minorista" | "mayorista"
    price?: number
    calculationParams?: Product["calculationParams"]
  }) {
    const newProduct: Product = {
      id: uuidv4(),
      code: product.code,
      name: product.name,
      type: product.type,
      price: product.price,
      calculationParams: product.calculationParams,
    }
    setProducts((prev) => [...prev, newProduct])
  }

  function updateProduct(id: string, updates: Partial<Product>) {
    setProducts((prev) =>
      prev.map((p) => (p.id === id ? { ...p, ...updates } : p))
    )
  }

  function updateMultipleProducts(updatedProducts: Product[]) {
    setProducts(updatedProducts)
  }

  function deleteProduct(id: string) {
    setProducts((prev) => prev.filter((p) => p.id !== id))
  }

  return (
    <main className="max-w-7xl mx-auto p-6">
      <h1 className="text-4xl font-bold mb-8 text-center">
        Sistema de Calculadora y Gestión de Productos
      </h1>
      <CalculatorForm
        onCalculation={(price, params) =>
          setCalculationResult({ price, params })
        }
      />
      <ProductForm
        onAddProduct={addProduct}
        calculatedPrice={calculationResult.price}
        calculationParams={calculationResult.params}
      />
      <MassParameterEditor
        products={products}
        onUpdateProducts={updateMultipleProducts}
      />
      <ProductList
        products={products}
        onUpdateProduct={updateProduct}
        onDeleteProduct={deleteProduct}
      />
    </main>
  )
}
