"use client"

import React, { useState } from "react"
import { calculateFinalPrice } from "@/lib/utils"

interface Product {
  id: string
  code: string
  name: string
  price?: number
  type: "minorista" | "mayorista"
  calculationParams?: {
    costoInicial: number
    gastos: number
    ganancia: number
    publicidad: number
    ingresosBrutos: number
    costoFijoML: number
    mercadolibre: number
    costoFinanciero: number
    pagoNube: number
    iva: number
    comisionTiendaNube: number
  }
}

interface ProductListProps {
  products: Product[]
  onUpdateProduct: (id: string, updates: Partial<Product>) => void
  onDeleteProduct: (id: string) => void
}

export default function ProductList({
  products,
  onUpdateProduct,
  onDeleteProduct,
}: ProductListProps) {
  const [activeTab, setActiveTab] = useState<"minorista" | "mayorista">("minorista")
  const [searchTerm, setSearchTerm] = useState("")
  const [editingId, setEditingId] = useState<string | null>(null)
  const [editForm, setEditForm] = useState({ code: "", name: "", price: "" })
  const [viewingDetailsId, setViewingDetailsId] = useState<string | null>(null)

  const filteredProducts = products
    .filter((product) => product.type === activeTab)
    .filter(
      (product) =>
        product.code.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.name.toLowerCase().includes(searchTerm.toLowerCase())
    )

  function handleEdit(product: Product) {
    setEditingId(product.id)
    setEditForm({
      code: product.code,
      name: product.name,
      price: product.price?.toString() || "",
    })
  }

  function handleSaveEdit() {
    if (editingId) {
      onUpdateProduct(editingId, {
        code: editForm.code,
        name: editForm.name,
        price: editForm.price ? parseFloat(editForm.price) : undefined,
      })
      setEditingId(null)
      setEditForm({ code: "", name: "", price: "" })
    }
  }

  function handleCancelEdit() {
    setEditingId(null)
    setEditForm({ code: "", name: "", price: "" })
  }

  function exportToCSV() {
    const csvContent = [
      "Código,Nombre,Precio,Tipo",
      ...filteredProducts.map(
        (product) =>
          `${product.code},${product.name},${product.price || ""},${product.type}`
      ),
    ].join("\n")

    const blob = new Blob([csvContent], { type: "text/csv" })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement("a")
    a.href = url
    a.download = `productos_${activeTab}.csv`
    a.click()
    window.URL.revokeObjectURL(url)
  }

  return (
    <div className="max-w-6xl mx-auto p-6 bg-white rounded-md shadow-md mt-8">
      <h2 className="text-2xl font-semibold mb-4">Gestión de Productos</h2>
      
      {/* Tabs */}
      <div className="flex border-b mb-4">
        <button
          onClick={() => setActiveTab("minorista")}
          className={`px-4 py-2 font-medium ${
            activeTab === "minorista"
              ? "border-b-2 border-black text-black"
              : "text-gray-600 hover:text-black"
          }`}
        >
          Minorista
        </button>
        <button
          onClick={() => setActiveTab("mayorista")}
          className={`px-4 py-2 font-medium ${
            activeTab === "mayorista"
              ? "border-b-2 border-black text-black"
              : "text-gray-600 hover:text-black"
          }`}
        >
          Mayorista
        </button>
      </div>

      {/* Search and Export */}
      <div className="flex flex-col md:flex-row gap-4 mb-4">
        <input
          type="text"
          placeholder="Buscar por código o nombre..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="flex-1 p-2 border rounded"
        />
        <button
          onClick={exportToCSV}
          className="px-4 py-2 bg-gray-800 text-white rounded hover:bg-gray-700 transition"
        >
          Exportar Listado
        </button>
      </div>

      {/* Products Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse border border-gray-300">
          <thead>
            <tr className="bg-gray-100">
              <th className="border border-gray-300 px-4 py-2 text-left">Código</th>
              <th className="border border-gray-300 px-4 py-2 text-left">Nombre</th>
              <th className="border border-gray-300 px-4 py-2 text-left">Precio</th>
              <th className="border border-gray-300 px-4 py-2 text-left">Parámetros</th>
              <th className="border border-gray-300 px-4 py-2 text-left">Acciones</th>
            </tr>
          </thead>
          <tbody>
            {filteredProducts.length === 0 ? (
              <tr>
                <td colSpan={5} className="border border-gray-300 px-4 py-8 text-center text-gray-500">
                  No hay productos registrados en {activeTab}
                </td>
              </tr>
            ) : (
              filteredProducts.map((product) => (
                <React.Fragment key={product.id}>
                  <tr>
                    <td className="border border-gray-300 px-4 py-2">
                      {editingId === product.id ? (
                        <input
                          type="text"
                          value={editForm.code}
                          onChange={(e) => setEditForm({ ...editForm, code: e.target.value })}
                          className="w-full p-1 border rounded"
                        />
                      ) : (
                        product.code
                      )}
                    </td>
                    <td className="border border-gray-300 px-4 py-2">
                      {editingId === product.id ? (
                        <input
                          type="text"
                          value={editForm.name}
                          onChange={(e) => setEditForm({ ...editForm, name: e.target.value })}
                          className="w-full p-1 border rounded"
                        />
                      ) : (
                        product.name
                      )}
                    </td>
                    <td className="border border-gray-300 px-4 py-2">
                      {editingId === product.id ? (
                        <input
                          type="number"
                          value={editForm.price}
                          onChange={(e) => setEditForm({ ...editForm, price: e.target.value })}
                          className="w-full p-1 border rounded"
                          placeholder="Precio"
                        />
                      ) : (
                        product.price ? `$${product.price.toFixed(2)}` : "Sin precio"
                      )}
                    </td>
                    <td className="border border-gray-300 px-4 py-2">
                      {product.calculationParams ? (
                        <button
                          onClick={() => setViewingDetailsId(viewingDetailsId === product.id ? null : product.id)}
                          className="px-2 py-1 bg-gray-600 text-white rounded text-sm hover:bg-gray-700"
                        >
                          {viewingDetailsId === product.id ? "Ocultar" : "Ver Detalle"}
                        </button>
                      ) : (
                        <span className="text-gray-500 text-sm">Sin parámetros</span>
                      )}
                    </td>
                    <td className="border border-gray-300 px-4 py-2">
                      {editingId === product.id ? (
                        <div className="flex gap-2">
                          <button
                            onClick={handleSaveEdit}
                            className="px-2 py-1 bg-green-600 text-white rounded text-sm hover:bg-green-700"
                          >
                            Guardar
                          </button>
                          <button
                            onClick={handleCancelEdit}
                            className="px-2 py-1 bg-gray-600 text-white rounded text-sm hover:bg-gray-700"
                          >
                            Cancelar
                          </button>
                        </div>
                      ) : (
                        <div className="flex gap-2">
                          <button
                            onClick={() => handleEdit(product)}
                            className="px-2 py-1 bg-blue-600 text-white rounded text-sm hover:bg-blue-700"
                          >
                            Editar
                          </button>
                          <button
                            onClick={() => onDeleteProduct(product.id)}
                            className="px-2 py-1 bg-red-600 text-white rounded text-sm hover:bg-red-700"
                          >
                            Eliminar
                          </button>
                        </div>
                      )}
                    </td>
                  </tr>
                  {viewingDetailsId === product.id && product.calculationParams && (
                    <tr>
                      <td colSpan={5} className="border border-gray-300 px-4 py-4 bg-gray-50">
                        <EditableParameters
                          product={product}
                          onUpdate={(updatedParams) => {
                            const newPrice = calculateFinalPrice(updatedParams)
                            onUpdateProduct(product.id, {
                              calculationParams: updatedParams,
                              price: newPrice,
                            })
                          }}
                        />
                      </td>
                    </tr>
                  )}
                </React.Fragment>
              ))
            )}
          </tbody>
        </table>
      </div>
    </div>
  )
}

interface EditableParametersProps {
  product: Product
  onUpdate: (updatedParams: {
    costoInicial: number
    gastos: number
    ganancia: number
    publicidad: number
    ingresosBrutos: number
    costoFijoML: number
    mercadolibre: number
    costoFinanciero: number
    pagoNube: number
    iva: number
    comisionTiendaNube: number
  }) => void
}

function EditableParameters({ product, onUpdate }: EditableParametersProps) {
  const [isEditing, setIsEditing] = useState(false)
  const [params, setParams] = useState(product.calculationParams!)
  const [error, setError] = useState<string | null>(null)

  function handleSave() {
    setError(null)
    try {
      onUpdate(params)
      setIsEditing(false)
    } catch (e) {
      if (e instanceof Error) {
        setError(e.message)
      }
    }
  }

  function handleCancel() {
    setParams(product.calculationParams!)
    setIsEditing(false)
    setError(null)
  }

  if (!isEditing) {
    return (
      <div>
        <div className="flex justify-between items-center mb-4">
          <h4 className="font-semibold">Parámetros de Cálculo</h4>
          <button
            onClick={() => setIsEditing(true)}
            className="px-3 py-1 bg-blue-600 text-white rounded text-sm hover:bg-blue-700"
          >
            Editar Parámetros
          </button>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <ul className="space-y-1 text-sm">
              <li>Costo Inicial: ${product.calculationParams!.costoInicial}</li>
              <li>Gastos: {product.calculationParams!.gastos}%</li>
              <li>Ganancia: ${product.calculationParams!.ganancia}</li>
              <li>Publicidad: {product.calculationParams!.publicidad}%</li>
              <li>Ingresos Brutos: {product.calculationParams!.ingresosBrutos}%</li>
              <li>Costo Fijo ML: ${product.calculationParams!.costoFijoML}</li>
            </ul>
          </div>
          <div>
            <ul className="space-y-1 text-sm">
              <li>MercadoLibre: {product.calculationParams!.mercadolibre}%</li>
              <li>Costo Financiero: {product.calculationParams!.costoFinanciero}%</li>
              <li>Pago Nube: {product.calculationParams!.pagoNube}%</li>
              <li>IVA: {product.calculationParams!.iva}%</li>
              <li>Comisión Tienda Nube: {product.calculationParams!.comisionTiendaNube}%</li>
            </ul>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div>
      <div className="flex justify-between items-center mb-4">
        <h4 className="font-semibold">Editar Parámetros de Cálculo</h4>
        <div className="flex gap-2">
          <button
            onClick={handleSave}
            className="px-3 py-1 bg-green-600 text-white rounded text-sm hover:bg-green-700"
          >
            Guardar
          </button>
          <button
            onClick={handleCancel}
            className="px-3 py-1 bg-gray-600 text-white rounded text-sm hover:bg-gray-700"
          >
            Cancelar
          </button>
        </div>
      </div>
      
      {error && <p className="text-red-600 text-sm mb-4">{error}</p>}
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-3">
          <label className="flex flex-col">
            <span className="text-sm font-medium">Costo Inicial</span>
            <input
              type="number"
              value={params.costoInicial}
              onChange={(e) => setParams({ ...params, costoInicial: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Gastos (%)</span>
            <input
              type="number"
              value={params.gastos}
              onChange={(e) => setParams({ ...params, gastos: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Ganancia ($)</span>
            <input
              type="number"
              value={params.ganancia}
              onChange={(e) => setParams({ ...params, ganancia: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Publicidad (%)</span>
            <input
              type="number"
              value={params.publicidad}
              onChange={(e) => setParams({ ...params, publicidad: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Ingresos Brutos (%)</span>
            <input
              type="number"
              value={params.ingresosBrutos}
              onChange={(e) => setParams({ ...params, ingresosBrutos: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Costo Fijo ML ($)</span>
            <input
              type="number"
              value={params.costoFijoML}
              onChange={(e) => setParams({ ...params, costoFijoML: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
        </div>
        <div className="space-y-3">
          <label className="flex flex-col">
            <span className="text-sm font-medium">MercadoLibre (%)</span>
            <input
              type="number"
              value={params.mercadolibre}
              onChange={(e) => setParams({ ...params, mercadolibre: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Costo Financiero (%)</span>
            <input
              type="number"
              value={params.costoFinanciero}
              onChange={(e) => setParams({ ...params, costoFinanciero: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Pago Nube (%)</span>
            <input
              type="number"
              value={params.pagoNube}
              onChange={(e) => setParams({ ...params, pagoNube: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">IVA (%)</span>
            <input
              type="number"
              value={params.iva}
              onChange={(e) => setParams({ ...params, iva: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
          <label className="flex flex-col">
            <span className="text-sm font-medium">Comisión Tienda Nube (%)</span>
            <input
              type="number"
              value={params.comisionTiendaNube}
              onChange={(e) => setParams({ ...params, comisionTiendaNube: Number(e.target.value) })}
              className="mt-1 p-2 border rounded text-sm"
            />
          </label>
        </div>
      </div>
    </div>
  )
}
