"use client"

import React, { useState } from "react"
import { calculateFinalPrice } from "@/lib/utils"

interface CalculatorFormProps {
  onCalculation?: (
    price: number | null,
    params: {
      costoInicial: number
      gastos: number
      ganancia: number
      publicidad: number
      ingresosBrutos: number
      costoFijoML: number
      mercadolibre: number
      costoFinanciero: number
      pagoNube: number
      iva: number
      comisionTiendaNube: number
    } | null
  ) => void
}

export default function CalculatorForm({ onCalculation }: CalculatorFormProps) {
  const [costoInicial, setCostoInicial] = useState(4800)
  const [gastos, setGastos] = useState(10)
  const [ganancia, setGanancia] = useState(3500)
  const [publicidad, setPublicidad] = useState(10)
  const [ingresosBrutos, setIngresosBrutos] = useState(5)
  const [costoFijoML, setCostoFijoML] = useState(1800)
  const [mercadolibre, setMercadolibre] = useState(16)
  const [costoFinanciero, setCostoFinanciero] = useState(5)
  const [pagoNube, setPagoNube] = useState(5)
  const [iva, setIva] = useState(21)
  const [comisionTiendaNube, setComisionTiendaNube] = useState(5)

  const [precioFinal, setPrecioFinal] = useState<number | null>(null)
  const [detalleCostos, setDetalleCostos] = useState<any>(null)
  const [error, setError] = useState<string | null>(null)

  function handleCalculate() {
    setError(null)
    try {
      const precio = calculateFinalPrice({
        costoInicial,
        ganancia,
        gastos,
        publicidad,
        ingresosBrutos,
        costoFijoML,
        mercadolibre,
        costoFinanciero,
        pagoNube,
        iva,
        comisionTiendaNube,
      })

      // Calcular detalle de costos
      const pagoNubeConIva = pagoNube * (1 + iva / 100)
      const costosCalculados = {
        gastosPesos: (precio * gastos) / 100,
        publicidadPesos: (precio * publicidad) / 100,
        ingresosBrutosPesos: (precio * ingresosBrutos) / 100,
        mercadolibrePesos: (precio * mercadolibre) / 100,
        costoFinancieroPesos: (precio * costoFinanciero) / 100,
        pagoNubePesos: (precio * pagoNubeConIva) / 100,
        comisionTiendaNubePesos: (precio * comisionTiendaNube) / 100,
        costoFijoMLPesos: costoFijoML,
        totalCostos: 0,
        gananciaNeta: 0,
      }

      costosCalculados.totalCostos =
        costosCalculados.gastosPesos +
        costosCalculados.publicidadPesos +
        costosCalculados.ingresosBrutosPesos +
        costosCalculados.mercadolibrePesos +
        costosCalculados.costoFinancieroPesos +
        costosCalculados.pagoNubePesos +
        costosCalculados.comisionTiendaNubePesos +
        costosCalculados.costoFijoMLPesos

      costosCalculados.gananciaNeta =
        precio - costosCalculados.totalCostos - costoInicial

      setPrecioFinal(precio)
      setDetalleCostos(costosCalculados)

      if (onCalculation) {
        onCalculation(precio, {
          costoInicial,
          gastos,
          ganancia,
          publicidad,
          ingresosBrutos,
          costoFijoML,
          mercadolibre,
          costoFinanciero,
          pagoNube,
          iva,
          comisionTiendaNube,
        })
      }
    } catch (e) {
      if (e instanceof Error) {
        setError(e.message)
      }
      setPrecioFinal(null)
      setDetalleCostos(null)
      if (onCalculation) {
        onCalculation(null, null)
      }
    }
  }

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded-md shadow-md">
      <h2 className="text-2xl font-semibold mb-4">Calculadora de Precio</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <label className="flex flex-col">
          Costo Inicial
          <input
            type="number"
            value={costoInicial}
            onChange={(e) => setCostoInicial(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Gastos (%)
          <input
            type="number"
            value={gastos}
            onChange={(e) => setGastos(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Ganancia ($)
          <input
            type="number"
            value={ganancia}
            onChange={(e) => setGanancia(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Publicidad (%)
          <input
            type="number"
            value={publicidad}
            onChange={(e) => setPublicidad(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Ingresos Brutos (%)
          <input
            type="number"
            value={ingresosBrutos}
            onChange={(e) => setIngresosBrutos(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Costo Fijo ML ($)
          <input
            type="number"
            value={costoFijoML}
            onChange={(e) => setCostoFijoML(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          MercadoLibre (%)
          <input
            type="number"
            value={mercadolibre}
            onChange={(e) => setMercadolibre(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Costo Financiero (%)
          <input
            type="number"
            value={costoFinanciero}
            onChange={(e) => setCostoFinanciero(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Pago Nube (%)
          <input
            type="number"
            value={pagoNube}
            onChange={(e) => setPagoNube(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          IVA (%)
          <input
            type="number"
            value={iva}
            onChange={(e) => setIva(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
        <label className="flex flex-col">
          Comisión Tienda Nube (%)
          <input
            type="number"
            value={comisionTiendaNube}
            onChange={(e) => setComisionTiendaNube(Number(e.target.value))}
            className="mt-1 p-2 border rounded"
          />
        </label>
      </div>
      <button
        onClick={handleCalculate}
        className="mt-6 px-4 py-2 bg-black text-white rounded hover:bg-gray-800 transition"
      >
        Calcular Precio
      </button>
      {error && <p className="mt-4 text-red-600">{error}</p>}
      {precioFinal !== null && !error && (
        <div className="mt-6">
          <div className="bg-gray-50 p-4 rounded-md">
            <h3 className="text-xl font-semibold mb-4">Precio Final Calculado: ${precioFinal.toFixed(2)}</h3>
            
            {detalleCostos && (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <h4 className="font-semibold mb-2">Detalle de Costos (en Pesos):</h4>
                  <ul className="space-y-1 text-sm">
                    <li>Gastos ({gastos}%): ${detalleCostos.gastosPesos.toFixed(2)}</li>
                    <li>Publicidad ({publicidad}%): ${detalleCostos.publicidadPesos.toFixed(2)}</li>
                    <li>Ingresos Brutos ({ingresosBrutos}%): ${detalleCostos.ingresosBrutosPesos.toFixed(2)}</li>
                    <li>MercadoLibre ({mercadolibre}%): ${detalleCostos.mercadolibrePesos.toFixed(2)}</li>
                    <li>Costo Financiero ({costoFinanciero}%): ${detalleCostos.costoFinancieroPesos.toFixed(2)}</li>
                    <li>Pago Nube ({pagoNube}% + {iva}% IVA): ${detalleCostos.pagoNubePesos.toFixed(2)}</li>
                    <li>Comisión Tienda Nube ({comisionTiendaNube}%): ${detalleCostos.comisionTiendaNubePesos.toFixed(2)}</li>
                    <li>Costo Fijo ML: ${detalleCostos.costoFijoMLPesos.toFixed(2)}</li>
                  </ul>
                </div>
                <div>
                  <h4 className="font-semibold mb-2">Resumen:</h4>
                  <ul className="space-y-1 text-sm">
                    <li>Precio de Venta: ${precioFinal.toFixed(2)}</li>
                    <li>Total Costos: ${detalleCostos.totalCostos.toFixed(2)}</li>
                    <li>Costo Inicial: ${costoInicial.toFixed(2)}</li>
                    <li className="font-semibold border-t pt-1">Ganancia Neta: ${detalleCostos.gananciaNeta.toFixed(2)}</li>
                    <li className="text-xs text-gray-600">Ganancia Esperada: ${ganancia.toFixed(2)}</li>
                  </ul>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  )
}
