"use client"

import React, { useState } from "react"

interface ProductFormProps {
  onAddProduct: (product: {
    code: string
    name: string
    type: "minorista" | "mayorista"
    price?: number
    calculationParams?: {
      costoInicial: number
      gastos: number
      ganancia: number
      publicidad: number
      ingresosBrutos: number
      costoFijoML: number
      mercadolibre: number
      costoFinanciero: number
      pagoNube: number
      iva: number
      comisionTiendaNube: number
    }
  }) => void
  calculatedPrice?: number | null
  calculationParams?: {
    costoInicial: number
    gastos: number
    ganancia: number
    publicidad: number
    ingresosBrutos: number
    costoFijoML: number
    mercadolibre: number
    costoFinanciero: number
    pagoNube: number
    iva: number
    comisionTiendaNube: number
  } | null
}

export default function ProductForm({
  onAddProduct,
  calculatedPrice,
  calculationParams,
}: ProductFormProps) {
  const [code, setCode] = useState("")
  const [name, setName] = useState("")
  const [type, setType] = useState<"minorista" | "mayorista">("minorista")
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)

  function handleSubmit(e: React.FormEvent) {
    e.preventDefault()
    setError(null)
    setSuccess(null)

    if (!code.trim() || !name.trim()) {
      setError("Por favor, complete ambos campos: código y nombre.")
      return
    }

    onAddProduct({
      code: code.trim(),
      name: name.trim(),
      type,
      price: calculatedPrice || undefined,
      calculationParams: calculationParams || undefined,
    })
    setCode("")
    setName("")
    setSuccess("Producto registrado correctamente.")
  }

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded-md shadow-md mt-8">
      <h2 className="text-2xl font-semibold mb-4">Registrar Producto</h2>
      {calculatedPrice && (
        <div className="mb-4 p-3 bg-green-50 border border-green-200 rounded-md">
          <p className="text-green-800 font-medium">
            Precio calculado disponible: ${calculatedPrice.toFixed(2)}
          </p>
          <p className="text-green-600 text-sm">
            Este precio y sus parámetros se guardarán automáticamente con el producto.
          </p>
        </div>
      )}
      <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-4 gap-4 items-end">
        <div className="flex flex-col">
          <label htmlFor="code" className="mb-1 font-medium">
            Código
          </label>
          <input
            id="code"
            type="text"
            value={code}
            onChange={(e) => setCode(e.target.value)}
            className="p-2 border rounded"
          />
        </div>
        <div className="flex flex-col">
          <label htmlFor="name" className="mb-1 font-medium">
            Nombre
          </label>
          <input
            id="name"
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="p-2 border rounded"
          />
        </div>
        <div className="flex flex-col">
          <label htmlFor="type" className="mb-1 font-medium">
            Tipo
          </label>
          <select
            id="type"
            value={type}
            onChange={(e) => setType(e.target.value as "minorista" | "mayorista")}
            className="p-2 border rounded"
          >
            <option value="minorista">Minorista</option>
            <option value="mayorista">Mayorista</option>
          </select>
        </div>
        <button
          type="submit"
          className="px-4 py-2 bg-black text-white rounded hover:bg-gray-800 transition"
        >
          Agregar Producto
        </button>
      </form>
      {error && <p className="mt-4 text-red-600">{error}</p>}
      {success && <p className="mt-4 text-green-600">{success}</p>}
    </div>
  )
}
